import { Fragment, FunctionComponent, ReactElement } from 'react';
import { Button, ButtonProps } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import cx from 'classnames';

const useStyles = makeStyles((theme) => ({
    root: {
        whiteSpace: 'nowrap'
    }
}));

interface K2FlatButtonExtendedProps {
    isHidden?: boolean;
}

export type K2FlatButtonProps = K2FlatButtonExtendedProps & ButtonProps;

export const K2FlatButton: FunctionComponent<K2FlatButtonProps> = ({ children, fullWidth = false, isHidden = false, ...props }): ReactElement => {
    const classes = useStyles();
    if (isHidden) {
        return <Fragment />;
    } else {
        return (
            <Button {...props} fullWidth={fullWidth} className={cx(classes.root, props.className)}>
                {children}
            </Button>
        );
    }
};
