import React, { forwardRef, FunctionComponent, ReactElement, useMemo, MouseEvent } from 'react';
import { Box, Tooltip, createStyles, makeStyles, MenuProps, useTheme } from '@material-ui/core';
import { K2Render } from 'k2-core/K2Render';
import { ICommandListOption, ICommandListComponent } from 'shared/command';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import clsx from 'classnames';
import { indigo } from '@material-ui/core/colors';

interface K2TextButtonItemProps extends MenuProps {
    label: string;
}

export interface TextButtonItemComponents extends ICommandListComponent {}

export const K2TextButtonItem: FunctionComponent<K2TextButtonItemProps> = (props): ReactElement => {
    return useMemo(() => new K2TextButtonItemClass(), []).display(props);
};

const useStyles = makeStyles((theme) =>
    createStyles({
        iconButton: {
            border: '1px solid #e6e6e6',
            padding: theme.spacing(0.5),
            borderRadius: theme.spacing(0.5),
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center'
        },
        buttonWithLabel: {
            ...theme.typography.button,
            backgroundColor: indigo[500],
            paddingRight: theme.spacing(2),
            color: theme.palette.common.white
        },
        disabledIconButton: {
            opacity: '0.25',
            cursor: 'default'
        }
    })
);

export class K2TextButtonItemClass<T extends K2TextButtonItemProps> extends K2Render<T> {
    Component: FunctionComponent<T> = (props): ReactElement => {
        return (
            <Box key={`option-${props.id}`} onClick={() => props.onClick} component="div" display="inline-block">
                {props.label}
            </Box>
        );
    };
}

export class K2TextButtonItemComponents implements TextButtonItemComponents {
    default = forwardRef((option: ICommandListOption, ref): ReactElement => {
        const theme = useTheme();
        const classes = useStyles();
        const MuiIcon = option.director.spec.muiIcon;
        const FaIcon = option.director.spec.faIcon;
        const label = option.director.label;
        return (
            <Box
                key={`option-${option.id}`}
                className={clsx(classes.iconButton, { [classes.buttonWithLabel]: Boolean(label) })}
                color="primary"
                onClick={() => option.director.start()}
            >
                {MuiIcon && <MuiIcon color="inherit" fontSize="small" />}
                {FaIcon && <FontAwesomeIcon icon={FaIcon} size="1x" color={theme.palette.common.white} />}
                <Box ml={0.5}>{label}</Box>
            </Box>
        );
    });
    tooltip = forwardRef((option: ICommandListOption, ref): ReactElement => {
        const theme = useTheme();
        const classes = useStyles();
        const MuiIcon = option.director.spec.muiIcon;
        const FaIcon = option.director.spec.faIcon;
        return (
            <Tooltip arrow title={option.director.label} placement="top">
                <Box key={`option-${option.id}`} className={clsx(classes.iconButton)} color="primary" onClick={() => option.director.start()}>
                    {MuiIcon && <MuiIcon color="inherit" fontSize="small" />}
                    {FaIcon && <FontAwesomeIcon icon={FaIcon} size="1x" color={theme.palette.common.white} />}
                </Box>
            </Tooltip>
        );
    });
    disabled = forwardRef((option: ICommandListOption, ref): ReactElement => {
        const theme = useTheme();
        const classes = useStyles();
        const MuiIcon = option.director.spec.muiIcon;
        const FaIcon = option.director.spec.faIcon;
        const label = option.director.label;

        return (
            <Box
                key={`option-${option.id}`}
                color="lightgrey"
                className={clsx('disabled', classes.iconButton, classes.disabledIconButton, { [classes.buttonWithLabel]: Boolean(label) })}
                onClick={(evt: MouseEvent<HTMLElement>) => {
                    evt.preventDefault();
                    evt.stopPropagation();
                }}
            >
                {MuiIcon && <MuiIcon color="inherit" fontSize="small" />}
                {FaIcon && <FontAwesomeIcon icon={FaIcon} size="1x" color={theme.palette.common.white} />}

                <Box ml={0.5}>{label}</Box>
            </Box>
        );
    });
}
