import { cPoint2D } from './foGeometry2D';
import { foGlyph2D } from './foGlyph2D.model';
import { iPoint2D } from './foInterface';
import { foObject } from './foObject.model';
import { PubSub } from './foPubSub';
import { foShape2D, IfoShape2DProperties } from './foShape2D.model';
import { foSelectionBuffer } from '../models/foBuffer';
import { Lifecycle } from './foLifecycle';

export interface IfoPageProperties extends IfoShape2DProperties {
    marginX?: number;
    marginY?: number;
    showBoundry?: boolean;
}

//a Shape is a graphic designed to behave like a visio shape
//and have all the same properties
export class foPage extends foShape2D implements IfoPageProperties {
    private _isDirty: boolean = true;
    gridSizeX: number = 50;
    gridSizeY: number = 50;
    showBoundry: boolean = true;
    protected selections: foSelectionBuffer = new foSelectionBuffer();

    constructor(properties?: IfoPageProperties, parent?: foObject) {
        super(properties, parent);
        this.override(properties);
    }

    markAsDirty(): foPage {
        this._isDirty = true;
        return this;
    }
    markAsClean(): foPage {
        this._isDirty = false;
        return this;
    }

    get isDirty() {
        return this._isDirty;
    }

    protected _marginX: number;
    get marginX(): number {
        return this._marginX || 0.0;
    }
    set marginX(value: number) {
        this.smash();
        this._marginX = value;
    }

    protected _marginY: number;
    get marginY(): number {
        return this._marginY || 0.0;
    }
    set marginY(value: number) {
        this.smash();
        this._marginY = value;
    }

    protected _scaleX: number;
    get scaleX(): number {
        return this._scaleX || 1.0;
    }
    set scaleX(value: number) {
        this.smash();
        this._scaleX = value;
    }

    protected _scaleY: number;
    get scaleY(): number {
        return this._scaleY || 1.0;
    }
    set scaleY(value: number) {
        this.smash();
        this._scaleY = value;
    }

    mouseLoc: any = {};

    findHitShape(hit: iPoint2D, deep: boolean = true, exclude: foGlyph2D = null): foShape2D {
        let found: foShape2D;

        for (let i: number = 0; i < this.subcomponents.length; i++) {
            const shape = this.subcomponents.getMember(i);
            if (shape === exclude) {
                continue;
            }
            found = shape.findObjectUnderPoint(hit, deep);
            if (found) {
                return found;
            }
        }
    }

    findShapeUnder(source: foGlyph2D, deep: boolean = true, exclude: foGlyph2D = null): foGlyph2D {
        // const frame = source.boundryFrame;
        for (let i: number = 0; i < this.subcomponents.length; i++) {
            let shape: foGlyph2D = this._subcomponents.getMember(i);
            if (source.hasAncestor(shape) || shape === exclude) {
                continue;
            }
            // if (shape.findObjectUnderFrame(source, frame, deep)) {
            //   return shape;
            // }
            return shape;
        }
    }

    public onItemHoverEnter = (loc: cPoint2D, shape: foGlyph2D, keys?: any): void => {};

    public onItemHoverExit = (loc: cPoint2D, shape: foGlyph2D, keys?: any): void => {};

    public onItemChangedPosition = (shape: foGlyph2D): void => {};

    zoomToCenter(g: cPoint2D, zoom: number, e: WheelEvent) {
        //you need to track this position in global space
        //so you can return it to the same location on the screen
        let pt1 = this.globalToLocalPoint(g);

        this.zoomBy(zoom);
        //page.updatePIP();

        //once the zoom is applied, measure where the global point has moved to
        //then pan back so it is in the center...
        const pt2 = this.localToGlobal(pt1.x, pt1.y);

        this.x += pt1.x - pt2.x;
        this.y += pt1.y - pt2.y;
        //console.log(pt2.x, pt2.y)

        //page.updatePIP();
    }

    setupMouseEvents() {
        let shape: foShape2D = null;
        let shapeUnder: foGlyph2D = null;
        let hovershape: foGlyph2D = null;
        let offset: iPoint2D = null;

        // let grab: foHandle2D = null;
        // let float: foHandle2D = null;

        // function debounce(func: (loc: cPoint2D, e: MouseEvent, keys: any) => void, wait = 50) {
        //     let h: any;
        //     return (loc: cPoint2D, e: MouseEvent, keys: any) => {
        //         clearTimeout(h);
        //         h = setTimeout(() => func(loc, e, keys), wait);
        //     };
        // }

        // const command = {
        //   d: this.duplicateSelected,
        //   c: this.copySelected,
        //   x: this.deleteSelected,
        //   v: this.pasteFromBuffer
        // };

        // PubSub.Sub('onkeydown', (e: KeyboardEvent, keys) => {
        //   if (keys.ctrl && e.key === 'd') {
        //     //duplicate
        //     this.duplicateSelected();
        //   } else if (keys.ctrl && e.key === 'c') {
        //     //copy
        //     this.copySelected();
        //   } else if (keys.ctrl && e.key === 'x') {
        //     //cut
        //     this.deleteSelected();
        //   } else if (keys.ctrl && e.key === 'v') {
        //     //paste
        //     this.pasteFromBuffer();
        //   } else if (keys.ctrl && e.key === 'l') {
        //     //connect
        //     this.connectSelected();
        //   } else if (keys.ctrl && e.key === 'g') {
        //     //group
        //     this.groupSelected();
        //   } else if (keys.ctrl && e.key === 'u') {
        //     //un-group
        //     this.unGroupSelected();
        //   } else if (keys.ctrl && e.key === 'a') {
        //     //select all
        //     this.selectAll();
        //   } else if (keys.ctrl && e.key === 's') {
        //     //save
        //     this.savePage();
        //   } else if (keys.ctrl && e.key === 'o') {
        //     //open
        //     this.openPage();
        //   } else {
        //     this.selections.sendKeysToShape(e, keys);
        //   }
        // });

        const mousedown = (loc: cPoint2D, e: MouseEvent, keys: any) => {
            this.onMouseLocationChanged(loc, 'down', keys);

            // grab = this.selections.findHandle(loc);
            // if (grab) {
            //     offset = grab.getOffset(loc);
            //     return;
            // }

            const found = this.findHitShape(loc);
            if (found) {
                shape = found;
                offset = shape.getOffset(loc);
                if (shape.isDragable()) {
                    this.subcomponents.moveToTop(shape);
                }
                this.selections.addSelection(shape, !keys.shift);
            } else {
                // grab = null;
                this.selections.clear();
            }
            //Lifecycle.pageSelections(this, this.selections);
        };
        PubSub.Sub('mousedown', mousedown);

        const mousemove = (loc: cPoint2D, e: MouseEvent, keys: any) => {
            if (shape && shape.isDragable()) {
                this.onMouseLocationChanged(loc, 'move', keys);
                shape.moveTo(loc, offset);
            } else {
                this.onMouseLocationChanged(loc, 'hover', keys);

                const found = this.findHitShape(loc) as foGlyph2D;
                if (found && found === hovershape) {
                    this.onItemHoverEnter(loc, hovershape);
                } else if (found) {
                    hovershape && this.onItemHoverExit(loc, hovershape);
                    hovershape = found;
                    this.onItemHoverEnter(loc, hovershape);
                } else {
                    hovershape && this.onItemHoverExit(loc, hovershape);
                    hovershape = null;
                }
            }
        };

        // do not user this const debounceMouseMove = debounce(mousemove, 10);
        PubSub.Sub('mousemove', mousemove);

        const mouseup = (loc: cPoint2D, e: MouseEvent, keys: any) => {
            // grab = null;
            this.onMouseLocationChanged(loc, 'up', keys);
            if (!shape) {
                return;
            }

            if (shape.isDragable()) {
                this._subcomponents.moveToTop(shape);
            }

            if (shapeUnder && keys.ctrl) {
                // //foObject.beep();
                // const { x, y } = shape.getLocation() as cPoint2D;
                // // const drop = shapeUnder.globalToLocal(x, y);
                // shapeUnder.addSubcomponent(shape.removeFromParent());
                // // shape.easeTo(drop.x, drop.y);
                // //shape.easeTo(0, 0);
                // shapeUnder = null;
                // this.onItemChangedParent(shape);
            } else {
                this.onItemChangedPosition(shape);
                Lifecycle.moveComplete(shape, loc);
            }

            // if (shape.myParent() !== this && keys.ctrl) {
            //     //foObject.beep();
            //     const { x, y } = shape.pinLocation();
            //     const drop = shape.localToGlobal(x, y);
            //     this.addSubcomponent(shape.removeFromParent());
            //     //shape.easeTo(drop.x, drop.y);
            //     this.onItemChangedParent(shape);
            // }

            shape = shapeUnder = null;
        };
        PubSub.Sub('mouseup', mouseup);

        PubSub.Sub('wheel', (loc: cPoint2D, g: cPoint2D, zoom: number, e: WheelEvent, keys) => {
            this.onMouseLocationChanged(loc, 'wheel', keys);
            if (keys.shift && keys.ctrl) {
                this.zoomToCenter(g, zoom, e);
            }
        });
    }

    public onMouseLocationChanged = (loc: cPoint2D, state: string, keys?: any): void => {
        this.mouseLoc = loc;
        this.mouseLoc.state = state;
        this.mouseLoc.keys = keys;
    };

    zoomBy(zoom: number) {
        this.scaleX *= zoom;
        this.scaleY *= zoom;
    }

    drawGrid(ctx: CanvasRenderingContext2D) {
        ctx.save();
        ctx.beginPath();

        ctx.setLineDash([5, 1]);
        ctx.strokeStyle = 'gray';

        const left = this.marginX - this.x;
        const top = this.marginY - this.y;
        const width = this.width / this.scaleX;
        const height = this.height / this.scaleY;
        const right = left + width;
        const bottom = top + height;

        //ctx.fillStyle = 'yellow';
        //ctx.fillRect(left,top, width, height);

        //draw vertical...
        let x = this.gridSizeX; //left;
        while (x < right) {
            ctx.moveTo(x, top);
            ctx.lineTo(x, bottom);
            x += this.gridSizeX;
        }
        x = -this.gridSizeX; //left;
        while (x > left) {
            ctx.moveTo(x, top);
            ctx.lineTo(x, bottom);
            x -= this.gridSizeX;
        }

        //draw horizontal...
        let y = this.gridSizeY; //top;
        while (y < bottom) {
            ctx.moveTo(left, y);
            ctx.lineTo(right, y);
            y += this.gridSizeY;
        }

        y = -this.gridSizeY; //top;
        while (y > top) {
            ctx.moveTo(left, y);
            ctx.lineTo(right, y);
            y -= this.gridSizeY;
        }

        ctx.stroke();
        ctx.restore();
    }

    drawAxis(ctx: CanvasRenderingContext2D) {
        ctx.save();
        ctx.beginPath();

        ctx.strokeStyle = 'black';
        ctx.lineWidth = 3;

        const left = this.marginX - this.x;
        const top = this.marginY - this.y;
        const width = this.width / this.scaleX;
        const height = this.height / this.scaleY;
        const right = left + width;
        const bottom = top + height;

        //draw vertical...
        ctx.moveTo(0, top);
        ctx.lineTo(0, bottom);

        //draw horizontal...

        ctx.moveTo(left, 0);
        ctx.lineTo(right, 0);

        ctx.stroke();
        ctx.restore();
    }

    drawPage(ctx: CanvasRenderingContext2D) {
        ctx.save();
        ctx.beginPath();

        ctx.strokeStyle = 'black';
        ctx.lineWidth = 5;

        //let left = this.marginX - this.x;
        //let top = this.marginY - this.y;
        //let width = this.width / this.scaleX;
        //let height = this.height / this.scaleY;
        //let right = left + width;
        //let bottom = top + height;

        //draw vertical...
        ctx.rect(0, 0, this.width, this.height);

        ctx.stroke();
        ctx.restore();
    }

    drawName(ctx: CanvasRenderingContext2D) {
        ctx.save();
        ctx.font = '50pt Calibri';
        ctx.lineWidth = 3;
        ctx.strokeStyle = 'blue';
        ctx.strokeText(this.myName, 10, 50);
        ctx.restore();
    }

    public render(ctx: CanvasRenderingContext2D, deep: boolean = true): foPage {
        ctx.clearRect(0, 0, this.width, this.height);

        ctx.save();

        this.drawName(ctx);

        this.preDraw && this.preDraw(ctx);
        this.draw(ctx);
        //this.drawHover && this.drawHover(ctx);
        this.postDraw && this.postDraw(ctx);

        deep &&
            this._subcomponents?.forEach((item) => {
                item.render(ctx, deep);
            });
        ctx.restore();

        this.showBoundry && this.afterRender(ctx);

        return this.markAsClean();
    }

    public preDraw = (ctx: CanvasRenderingContext2D): void => {
        ctx.fillStyle = this.color;
        ctx.fillRect(0, 0, this.width, this.height);
    };

    public draw = (ctx: CanvasRenderingContext2D): void => {
        this.drawGrid(ctx);
        this.drawAxis(ctx);
        this.drawPage(ctx);
        this.drawPin(ctx);
    };
}
