import { Fragment, FunctionComponent, ReactElement } from 'react';
import { Canvas } from 'Canvas';
import { K2Render } from 'k2-core/K2Render';
import { ParkTheatreCanvasPage } from './park-theatre-canvas.model';
import { Box } from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';
import { ToJSON } from 'core/foRenderer';

class ViewModel {}
export class ParkTheatreRender<T extends ViewModel> extends K2Render<T> {
    model: typeof ParkTheatreCanvasPage;
    actionButtons: ReactElement[];
    platformJSON: any;
    Component: FunctionComponent<T> = (props): ReactElement => {
        return (
            <Fragment>
                <Box display="flex" flexWrap="wrap">
                    {this.actionButtons}
                </Box>
                <Canvas {...this.model.canvas2Params} />

                <Hidden only="xs">
                    <ToJSON>{this.model.asPlatform(true)}</ToJSON>
                </Hidden>
            </Fragment>
        );
    };
}
