import { EmptyRender } from 'core/foRenderer';
import { createElement } from 'react';

export class K2Render<T> {
    props: T = {} as T;
    Component: React.FunctionComponent<T> = EmptyRender;

    display(props?: T) {
        if (props) {
            this.props = props;
        }
        return createElement(this.Component, this.props);
    }
}

export class K2Container<T> extends K2Render<T> {}
