import { foImage2D } from 'foundry/models/foImage2D.model';
import { iPoint2D } from 'foundry/models/foInterface';

export class IobtCanvasShape2D extends foImage2D {
    protected localHitTest = (hit: any): boolean => {
        const { x, y } = hit as iPoint2D;

        const left = this.x - this.pinX();
        if (x < left) return false;

        const right = this.x + this.pinX();
        if (x > right) return false;

        const top = this.y - this.pinY();
        if (y < top) return false;

        const bottom = this.y + this.pinY();
        if (y > bottom) return false;

        return true;
    };
}
