import { UDTO_Body, UDTO_Platform } from 'models/UDTO-message';
import { CentralModelService } from 'services/central-model.service';

export class ParkTheatreMessage {
    sendPlatformMessage(message: UDTO_Platform) {
        console.log('send platform message');
        CentralModelService.sendPlatform(message);
    }
    sendBody(message: UDTO_Body) {
        console.log('send body message');
        CentralModelService.sendBody(message);
    }
}
