import { Fragment, FunctionComponent, ReactElement } from 'react';
import { K2Render } from 'k2-core/K2Render';
import { CentralModelService } from 'services/central-model.service';
import Hidden from '@material-ui/core/Hidden';
import { ToJSON } from 'core/foRenderer';

class ViewModel {}
export class DisplayLogosRender<T extends ViewModel> extends K2Render<T> {
    cService: typeof CentralModelService;
    Component: FunctionComponent<T> = (props): ReactElement => {
        return (
            <Fragment>
                <div>Display Logos</div>
                <Hidden only="xs">
                    <ToJSON>{CentralModelService.sharedModel}</ToJSON>
                </Hidden>
            </Fragment>
        );
    };
}
