import { Content, DivIcon, LatLngTuple, LeafletMouseEvent, Map, PopupOptions } from 'leaflet';
import { IMessageView, UDTO_Objective, UDTO_Observation, UDTO_Platform } from './UDTO-message';
import { BBox } from '@turf/turf';
import { Tools } from 'shared/foTools';

export class GeolocationHelper {
    static cloneCoords(position: GeolocationPosition): GeolocationCoordinates {
        const coords = position.coords;
        return Object.assign(
            {},
            {
                latitude: coords.latitude,
                longitude: coords.longitude,
                altitude: coords.altitude,
                heading: coords.heading,
                accuracy: coords.accuracy,
                altitudeAccuracy: coords.altitudeAccuracy,
                speed: coords.speed
            }
        );
    }
}

export class MapviewHelper {
    static mapLayerColors = ['red', 'orange', 'green', 'blue', 'purple'];

    static getColor(index: number) {
        if (MapviewHelper.mapLayerColors[index]) {
            return MapviewHelper.mapLayerColors[index];
        } else {
            return MapviewHelper.mapLayerColors[0];
        }
    }

    static videoSpec: { content: Content; options: PopupOptions } = {
        content:
            '<iframe width="560" height="315" src="https://www.youtube.com/embed/Lu56xVlZ40M" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>',
        options: {
            minWidth: 580,
            maxHeight: 350,
            className: 'iobt'
        }
    };

    static getCallSignIcon(personId: string): DivIcon {
        return new DivIcon({
            html: `<div class="call-sign-icon">${personId}</div>`
        });
    }

    static getObjectiveIcon(objective: UDTO_Objective): DivIcon {
        return new DivIcon({
            html: `<div class="objective-container"><img src="${objective.name}" class="objective-container-img" data-type="${objective.type}" /></div>`,
            className: 'non-existent-class-to-prevent-label-box'
        });
    }

    static getMoveObjective(objective: UDTO_Objective, evt: LeafletMouseEvent): UDTO_Objective {
        objective.lat = evt.target.getLatLng().lat;
        objective.lng = evt.target.getLatLng().lng;
        return objective;
    }

    static getMoveObservation(observation: UDTO_Observation, evt: LeafletMouseEvent): UDTO_Observation {
        observation.lat = evt.target.getLatLng().lat;
        observation.lng = evt.target.getLatLng().lng;
        return observation;
    }

    static getMovePlatform(platform: UDTO_Platform, evt: LeafletMouseEvent): UDTO_Platform {
        platform.lat = evt.target.getLatLng().lat;
        platform.lng = evt.target.getLatLng().lng;
        return platform;
    }

    static getPlatformIconBBox(map: Map, platform: UDTO_Platform, bbox: BBox): DivIcon {
        return MapviewHelper.getScalableRectangleBBox(map, platform.platformName, bbox);
    }

    static getObservationIconBBox(map: Map, observation: UDTO_Observation, bbox: BBox): DivIcon {
        return MapviewHelper.getScalableCircleBBox(map, observation.target, bbox);
    }

    static getScalableCircleBBox(map: Map, name: string, bbox: BBox): DivIcon {
        const startPoint = [bbox[3], bbox[2]] as LatLngTuple;
        const endPoint = [bbox[1], bbox[0]] as LatLngTuple;
        console.log('getScalableCircleBBox startPoint, bbox=', startPoint, bbox);

        const zoom = map.getZoom();
        const startPixelPoint = map.project(startPoint, zoom);
        const endPixelPoint = map.project(endPoint, zoom);

        const pxW = Number(Number(endPixelPoint.y - startPixelPoint.y).toFixed(2));
        const pxH = pxW;
        const fontSize = Math.min(pxW / 8, 12);

        const containerStyles = [`width: ${pxW}px`, `height: ${pxH}px`].join(';');
        const textStyles = [`font-size: ${fontSize}pt`].join(';');

        return new DivIcon({
            html: `<div style="${containerStyles}" class="observation-container"><div style="${textStyles}" class="observation-text">${name}</div></div>`,
            iconAnchor: [pxW / 2, pxH / 2],
            className: 'non-existent-class-to-prevent-label-box'
        });
    }

    static getScalableRectangleBBox(map: Map, name: string, bbox: BBox): DivIcon {
        const startPoint = [bbox[3], bbox[2]] as LatLngTuple;
        const endPoint = [bbox[1], bbox[0]] as LatLngTuple;
        console.log('getScalableRectangleBBox startPoint, bbox=', startPoint, bbox);

        const zoom = map.getZoom();
        const startPixelPoint = map.project(startPoint, zoom);
        const endPixelPoint = map.project(endPoint, zoom);

        const pxW = Number(Number(endPixelPoint.x - startPixelPoint.x).toFixed(2));
        const pxH = Number(Number(endPixelPoint.y - startPixelPoint.y).toFixed(2));
        const fontSize = Math.min(pxW / 8, 12);

        const containerStyles = [`width: ${pxW}px`, `height: ${pxH}px`].join(';');
        const textStyles = [`font-size: ${fontSize}pt`].join(';');

        return new DivIcon({
            html: `<div style="${containerStyles}" class="platform-container"><div style="${textStyles}" class="observation-text">${name}</div></div>`,
            iconAnchor: [pxW / 2, pxH / 2],
            className: 'non-existent-class-to-prevent-label-box'
        });
    }

    static getRandomPositionMove(): [number, number] {
        const randomMoves: Array<[number, number]> = [
            [0.0001, 0],
            [-0.0001, 0],
            [0, 0.0001],
            // [0, -0.0001],
            [0.00015, 0],
            [-0.00015, 0],
            [0, 0.00015],
            // [0, -0.0001],
            [0.000175, 0],
            [-0.000175, 0],
            [0, 0.000175]
            // [0, -0.0001]
        ];
        return randomMoves[Tools.randomInt(0, randomMoves.length - 1)];
    }
}

export class MapMessageDisplayer {
    messages: IMessageView[] = [];

    appendDisplayMessage(data: any) {
        this.messages.unshift(data.getDisplayInfo());
    }
}
