// https://medium.com/@pdx.lucasm/canvas-with-react-js-32e133c05258

import { FunctionComponent, ReactElement, useState, useEffect } from 'react';
import { useCanvas2D } from 'CanvasHook';
import { PubSub } from './foundry/models/foPubSub';
import { cPoint2D } from 'foundry/models/foGeometry2D';
import { Box } from '@material-ui/core';

export const Canvas: FunctionComponent<any> = (props: any): ReactElement => {
    const { draw, ...rest } = props;
    const canvasRef = useCanvas2D(props);
    const setMouseLoc = useState<any>(undefined)[1];
    const forceRefresh = useState<any>(0)[1];

    useEffect(() => {
        PubSub.Sub('mousemove', (loc: cPoint2D, e: MouseEvent, keys: any) => {
            forceRefresh(new Date().getTime());
            setMouseLoc(loc);
        });
    }, [setMouseLoc, forceRefresh]);

    return (
        <Box>
            <h1>{props.title}</h1>
            {/* <div>X: {mouseLoc?.x}</div>
            <div>Y: {mouseLoc?.y}</div> */}
            <canvas ref={canvasRef} {...rest} />
        </Box>
    );
};
