import { ForwardRefExoticComponent, RefAttributes } from 'react';
import { PubSub, Topic } from 'shared/K2PubSub';
import { OverridableComponent } from '@material-ui/core/OverridableComponent';
import { SvgIconTypeMap } from '@material-ui/core';
import { IconName } from '@fortawesome/fontawesome-svg-core';
import { SaveAndClose } from 'k2-core/icons/SaveAndClose';
import { K2MenuItemComponents } from 'k2-mui-core/K2MenuItem';
import { K2IconButtonItemComponents } from 'k2-mui-core/K2IconButtonItem';
import { K2TextButtonItemComponents } from 'k2-mui-core/K2TextButtonItem';

export type RenderComponent = ForwardRefExoticComponent<ICommandListOption & RefAttributes<unknown>>;

export interface ICommandItemComponentContainer {
    itemComponents: ICommandListComponent;
}

export type ApplicationItemComponentContainer = K2MenuItemComponents & K2IconButtonItemComponents & K2TextButtonItemComponents;
export type ApplicationItemComponents = K2MenuItemComponents & K2IconButtonItemComponents & K2TextButtonItemComponents;

export interface ICommandListComponent {
    default: RenderComponent;
    disabled: RenderComponent;
}

export interface ICommandListOption {
    id: string;
    director: CommandListDirector;
}

export type CommandListDirectorSpec = {
    label: string;
    muiIcon?: OverridableComponent<SvgIconTypeMap<{}, 'svg'>> | typeof SaveAndClose;
    faIcon?: IconName;
    isLog?: boolean;
};

type DirectorGenerator = Generator<unknown, string, string>;
abstract class CommandItem {
    director: CommandListDirector;
    name: string = 'Unnamed';
    abstract execute: () => void;
}

export interface IDialogPageCommand {
    pathname: string;
    dialogProps: any;
    onOk?: Function;
    onCancel?: Function;
    name?: string;
}

class DialogPageCommand extends CommandItem implements IDialogPageCommand {
    pathname: string;
    dialogProps: any;
    onOk: Function;
    onCancel: Function;
    execute = () => {
        const ok = () => {
            this.onOk && this.onOk();
            this.director.next('OK');
        };
        const cancel = () => {
            this.onCancel && this.onCancel();
        };
        PubSub.broadcastPayload(Topic.DISPLAY_OVERLAY_PAGE, {
            pathname: this.pathname,
            props: { ok, cancel, dialogProps: this.dialogProps, director: this.director }
        });
    };

    static default: IDialogPageCommand = {
        pathname: '/confirm-info-add-offering-page',
        dialogProps: {},
        name: 'defaultDialogPageCommand'
    };

    constructor(properties: IDialogPageCommand) {
        super();
        Object.assign(this, { ...properties });
    }
}

export interface ISynchronousCommand {
    action: Function;
    name?: string;
}

export type IASynchronousCommand = ISynchronousCommand;

class SynchronousCommand extends CommandItem implements ISynchronousCommand {
    action: (director: CommandListDirector) => any;
    execute = () => {
        this.action && this.action(this.director);
        window.setTimeout(() => {
            this.director.next('SYNC COMMAND');
        }, 0);
    };
    constructor(properties: ISynchronousCommand) {
        super();
        Object.assign(this, { ...properties });
    }
}

class ASynchronousCommand extends CommandItem implements ISynchronousCommand {
    action: (director: CommandListDirector) => any;
    execute = () => {
        this.action && this.action(this.director);
    };
    constructor(properties: ISynchronousCommand) {
        super();
        Object.assign(this, { ...properties });
    }
}

class TimeoutCommand extends CommandItem implements ISynchronousCommand {
    action: (director: CommandListDirector) => any;
    delayMs: number = 0;
    execute = () => {
        window.setTimeout(() => {
            this.action && this.action(this.director);
        }, this.delayMs);
    };
    constructor(properties: ISynchronousCommand) {
        super();
        Object.assign(this, { ...properties });
    }
}

/**
 * Roughly based on Builder Pattern (hence the suffix "Director")
 * https://refactoring.guru/design-patterns/builder
 *
 */
export class CommandListDirector {
    label: string;
    isLog = false;
    spec: CommandListDirectorSpec;
    [key: string]: any;
    private commands: CommandItem[] = [];
    private iterator: DirectorGenerator;

    private add<T extends CommandItem>(command: T) {
        command.director = this;
        this.commands.push(command);
        return this;
    }

    private addTimeout(spec: IASynchronousCommand, delayMs: number = 0) {
        const command = new TimeoutCommand(spec);
        command.delayMs = delayMs;
        return this.add(command);
    }

    private *coroutine(director: CommandListDirector): DirectorGenerator {
        for (const command of director.commands) {
            director.isLog && console.log(`CommandListDirector before execute command name="${command.name}"`);
            command.execute();

            const yieldValue = yield `Command name: ${command.name}`;
            director.isLog && console.log('CommandListDirector yieldValue=', yieldValue);
        }
        return 'START';
    }

    /**
     * Display a dialog page as part of the command list chain.
     */
    addDialogPage(spec: IDialogPageCommand = DialogPageCommand.default, conditionalRender: boolean = true) {
        if (conditionalRender) {
            const command = new DialogPageCommand(spec);
            return this.add(command);
        }
        return this;
    }

    /**
     * Sync commands execute and upon completion, director.next is automatically executed by the director.
     */
    addSync(spec: ISynchronousCommand) {
        const command = new SynchronousCommand(spec);
        return this.add(command);
    }

    /**
     * Async commands must execute director.next at the time you deem appropriate.
     */
    addASync(spec: IASynchronousCommand) {
        const command = new ASynchronousCommand(spec);
        return this.add(command);
    }

    /**
     * Decide whether or not to execute the next command.  The command chain will stop of you do not execute next.
     */
    addConditional(spec: ISynchronousCommand) {
        return this.addTimeout(spec, 0);
    }

    /**
     * Primary purpose is to wait a bit between sequential dialog commands.  You may, of course, sleep for any reason you can think of.
     */
    addSleep(delayMs: number = 250, name: string = 'sleepBetweenDialogs') {
        const spec: IASynchronousCommand = {
            action: (director: CommandListDirector) => {
                director.next('SLEEP');
            },
            name
        };
        this.addTimeout(spec, delayMs);
        return this;
    }

    /**
     * Your custom function with logic to determine which component type to render.
     */
    getRender: (render: any) => RenderComponent = (render: ApplicationItemComponents) => {
        return render.default;
    };

    /**
     * Determine which component type to render.
     */
    renderRule(func: (render: ApplicationItemComponents) => RenderComponent) {
        this.getRender = func;
        return this;
    }

    /**
     * Your custom function with logic to determine whether or not the command belongs in the chain.
     */
    existsWhenFunc: () => boolean = () => {
        return true;
    };

    /**
     * Determine whether or not this command is added to the command chain during command list construction.
     * @default true
     */
    existsWhenRule(existsWhenFunc: () => boolean) {
        this.existsWhenFunc = existsWhenFunc;
        return this;
    }

    /**
     * Perform any action you wish before going on to next command.
     *
     * This is effectively a Sync command, but the intent is to perform actions that are outside of the intent of the command list.
     *
     * Examples of things to "do" are unsubscribe from a previously executed Async command or compute values (and writeCustomProperty) for the next command.
     */
    do(func: (director: CommandListDirector) => any) {
        const spec: IASynchronousCommand = {
            action: (actionDirector: CommandListDirector) => {
                func(this);
                actionDirector.next('DO');
            },
            name: 'DO FUNC'
        };
        this.addTimeout(spec);
        return this;
    }

    /**
     * Create a new property/value for use within commands that come later in the chain.
     * @throws Unable to Define Property Eror
     */
    writeCustomProperty(propertyName: string, propertyValue: any): CommandListDirector {
        if (Reflect.set(this, propertyName, propertyValue)) {
            return this;
        } else {
            throw new Error(`${propertyName} was not able to be created.`);
        }
    }

    /**
     * Return value for requested property
     * @throws Unknown Property Error
     */
    readCustomProperty(propertyName: string): any {
        if (Reflect.getOwnPropertyDescriptor(this, propertyName)) {
            return this[propertyName];
        } else {
            throw new Error(`${propertyName} is not a known custom property on this command list.  You may need to set a property with writeCustomProperty.`);
        }
    }

    /**
     * Return boolean based on if property is present
     * @throws Unknown Property Error
     */
    hasCustomProperty(propertyName: string): any {
        return Reflect.getOwnPropertyDescriptor(this, propertyName);
    }

    /**
     * Need to execute start to initialize the command chain.
     */
    start() {
        this.iterator = this.coroutine(this);
        this.iterator.next('START_ITERATOR');
    }

    /**
     * Execute the next command in the chain.
     */
    next(value: string) {
        const yieldValue = this.iterator.next(value);
        this.isLog && console.log('next method yieldValue=', yieldValue);
    }

    /**
     * Stop the command chain.
     */
    end(value?: string) {
        const endVal = value ? value : 'END';
        this.iterator.return(endVal);
    }

    /**
     * @deprecated - is this needed?
     */
    construct() {
        return this;
    }

    constructor(properties: CommandListDirectorSpec) {
        Object.assign(this, { ...properties });
        this.spec = properties;
    }

    static getInstance(properties: CommandListDirectorSpec) {
        const director = new CommandListDirector(properties);
        return director;
    }
}
