import { Observable, Subject, BehaviorSubject } from 'rxjs';
import { filter } from 'rxjs/operators';

export enum Topic {
    DEFAULT,
    SIGNAL_R_CONNECTION_ESTABLISHED,
    SIGNAL_R_CONNECTION_UNAVAILABLE,
    RECEIVED_COP_UPDATE,
    RECEIVED_CHAT_MESSAGE,
    RECEIVED_ZOOM_IN_COMMAND,
    RECEIVED_ZOOM_OUT_COMMAND,
    RECEIVED_HIDE_MAP_COMMAND,
    RECEIVED_SHOW_MAP_COMMAND,
    DISPLAY_OVERLAY_PAGE,
    MOVE_COMPLETE
}

// use tuple
// https://www.tutorialsteacher.com/typescript/typescript-tuple

class K2PubSub<T extends Topic> {
    private _behaviorSubject = new BehaviorSubject<Topic>(Topic.DEFAULT);
    private _subject = new Subject<T>();
    private _subjectPayload = new Subject<[T, any]>();

    pubSub$(id: Topic): Observable<T> {
        return this._subject.pipe(filter((x) => x === id));
    }

    pubSubLateObserver$(id: Topic): Observable<Topic> {
        return this._behaviorSubject.pipe(filter((x) => x === id));
    }

    pubSubRaw$(): Observable<T> {
        return this._subject;
    }

    pubSubCase$(ids: Topic[]): Observable<T> {
        return this._subject.pipe(filter((x) => ids.indexOf(x) !== -1));
    }

    broadcast(topic: T) {
        this._subject.next(topic);
    }

    broadcastToLateObservers(topic: T) {
        this._behaviorSubject.next(topic);
    }

    pubSubPayload$(id: Topic): Observable<[T, any]> {
        return this._subjectPayload.pipe(filter((obj) => obj[0] === id));
    }

    pubSubCasePayload$(ids: Topic[]): Observable<[T, any]> {
        return this._subjectPayload.pipe(filter((obj) => ids.indexOf(obj[0]) !== -1));
    }

    broadcastPayload(topic: T, payload: any) {
        this._subjectPayload.next([topic, payload]);
    }
}

export let PubSub: K2PubSub<Topic> = new K2PubSub<Topic>();
