import { FunctionComponent, ReactElement, useState, useEffect, useMemo } from 'react';
import { useTheme } from '@material-ui/core/styles';
import { ParkTheatreRender } from './park-theatre.render';
import { ParkTheatreCanvasPage } from './park-theatre-canvas.model';
import { ParkTheatreCommand } from './park-theatre-command.model';
import { CentralModelService } from 'services/central-model.service';
import { PubSub, Topic } from 'shared/K2PubSub';
import { BreakpointValues } from '@material-ui/core/styles/createBreakpoints';
import { Size, useWindowSize } from 'shared/WindowResizeHook';
import { Lifecycle } from 'foundry/models/foLifecycle';

class ViewProps {
    containerMaxWidthBreakpoint: keyof BreakpointValues;
}

// NOTE: I did not use a Component Class because React 17 does not allow us to use hooks inside of Typescript Classes.
export const ParkTheatre: FunctionComponent<ViewProps> = (props): ReactElement => {
    const theme = useTheme();
    const windowSize: Size = useWindowSize();
    const setRefreshMs = useState<number>(0)[1];

    const Render = useMemo((): ParkTheatreRender<any> => new ParkTheatreRender(), []);
    Render.model = ParkTheatreCanvasPage;

    const command = useMemo((): ParkTheatreCommand => new ParkTheatreCommand(Render.model), [Render.model]);
    Render.actionButtons = command.getTextButtons();

    useEffect(() => {
        window.setTimeout(() => {
            CentralModelService.sendPing();
            Lifecycle.event('pageCreated', Render.model);
        }, 1000);
    }, [Render.model]);

    useEffect(() => {
        const getAvailableScreenWidth = (): number => {
            return Math.min(theme.breakpoints.values[props.containerMaxWidthBreakpoint], windowSize.width);
        };
        ParkTheatreCanvasPage.init(getAvailableScreenWidth);
    }, [windowSize, theme, props.containerMaxWidthBreakpoint]);

    useEffect(() => {
        // Only need this to display updated JSON on screen; don't need this in "production"
        PubSub.pubSub$(Topic.RECEIVED_COP_UPDATE).subscribe(() => {
            setRefreshMs(new Date().getTime());
        });
    }, [setRefreshMs]);

    return Render.display();
};
