import { Screen2D } from 'foundry/drivers/canvasDriver';
import { useRef, useEffect } from 'react';

export function useCanvas2D(props: any) {
    const draw = props.draw;
    const canvasRef = useRef<HTMLCanvasElement>(null);

    // <div>MouseLoc: {JSON.stringify(model.mouseLoc)}</div>
    // <div>Mouse State: {model.mouseLoc.state}</div>
    // <div>Mouse Keys: {JSON.stringify(model.mouseLoc.keys)}</div>

    useEffect(() => {
        let animationFrameId: number = 0;

        if (canvasRef.current) {
            const canvas: HTMLCanvasElement = canvasRef.current;
            const context: CanvasRenderingContext2D | null = canvas.getContext('2d');
            const screen2D: Screen2D = new Screen2D();
            screen2D.setRoot(canvas, props.width, props.height);

            const render = () => {
                if (context) {
                    // console.log('props=', props.model.mouseLoc);

                    draw(context, animationFrameId);
                    animationFrameId = window.requestAnimationFrame(render);
                }
            };
            render();
        }

        return () => {
            window.cancelAnimationFrame(animationFrameId);
        };
    }, [draw, props]);

    return canvasRef;
}
