import { Tools, foNames } from './foTools';
import { iObject, ModelRef } from './foInterface';
//import { setTimeout } from 'timers';

export interface IDisposable {
    dispose(): any;
}

export function using<T extends IDisposable>(resource: T, func: (resource: T) => any) {
    let result: any;
    try {
        result = func(resource);
    } finally {
        resource.dispose();
    }
    return result;
}

export class foObject implements iObject {
    static beep() {
        const snd = new Audio(
            // tslint:disable-next-line:max-line-length
            'data:audio/wav;base64,'
        );
        snd.play();
    }

    static jsonAlert(obj: any, title: string = 'JSON Alert') {
        try {
            alert(title + '\r' + JSON.stringify(obj, undefined, 3));
        } catch (ex) {
            alert(title + '\r' + JSON.stringify(obj.asJson, undefined, 3));
            alert(ex);
        }
    }

    private _myGuid: string;
    myName: string = foNames.UNKNOWN;
    myParent: ModelRef<iObject> | undefined;

    private _isVisible: boolean = true;
    get isVisible(): boolean {
        return this._isVisible;
    }
    set isVisible(value: boolean) {
        this._isVisible = value;
    }
    get isInvisible(): boolean {
        return !this._isVisible;
    }
    set isInvisible(value: boolean) {
        this._isVisible = !value;
    }

    private _isHitable: boolean = true;
    get isHitable(): boolean {
        return this._isHitable;
    }
    set isHitable(value: boolean) {
        this._isHitable = value;
    }

    show(value: boolean = true) {
        this._isVisible = value ? true : false;
        return this;
    }
    hide() {
        this._isVisible = false;
        return this;
    }

    get isPublic() {
        return this._isVisible;
    }

    constructor(properties?: any, parent?: foObject) {
        if (parent) {
            this.myParent = () => {
                return parent;
            };
        }

        this.override(properties);
    }
    myClass: string;

    //https://www.npmjs.com/package/reflect-metadata
    //https://stackoverflow.com/questions/13613524/get-an-objects-class-name-at-runtime-in-typescript

    get myType(): string {
        const comp: any = this.constructor;
        return comp.name;
    }
    set myType(ignore: string) {}

    get myGuid(): string {
        if (!this._myGuid) {
            this._myGuid = Tools.generateUUID();
        }
        return this._myGuid;
    }

    set myGuid(value) {
        if (!this._myGuid) {
            this._myGuid = value;
        }
    }

    public generateName() {
        const short = this.myGuid.slice(-6);
        this.myName = `${this.myType}_${short}`;
        return this;
    }

    public defaultName(name?: string) {
        if (name) {
            this.myName = name;
        } else if (Tools.matches(this.myName, foNames.UNKNOWN)) {
            this.generateName();
        }
        return this;
    }

    protected _displayName: string;
    get displayName() {
        if (this._displayName) {
            return this._displayName;
        }
        return `${this.myName} - ${this.myType}`;
    }
    set displayName(value: string) {
        this._displayName = value;
    }

    setName(name: string) {
        this.myName = name;
        return this;
    }

    is2D() {
        return false;
    }
    is3D() {
        return false;
    }

    asReference(): string {
        const parent = this.myParent && this.myParent();
        if (!parent) {
            return foNames.ROOT;
        }
        return `${this.myName}.${parent.asReference()}`;
    }

    then(next: (obj: any) => void) {
        next(this);
        return this;
    }

    hasAncestor(member: iObject): boolean {
        if (member === this) {
            return true;
        }

        const parent = this.myParent && this.myParent();
        if (member === parent) {
            return true;
        }
        return Boolean(parent && parent?.hasAncestor(member));
    }

    get hasParent() {
        const parent = this.myParent && this.myParent();
        return parent ? true : false;
    }

    removeParent(parent: foObject) {
        if (this.hasParent && this.myParent && this.myParent() === parent) {
            this.myParent = undefined;
        }
        return this.hasParent;
    }

    setParent(newParent: foObject) {
        const parent = this.myParent && this.myParent();
        if (parent !== newParent) {
            this.myParent = () => {
                return newParent;
            };
        }
        return this;
    }

    public wait(time: number, func: () => void) {
        setTimeout(func, time);
        return this;
    }

    public getChildAt(i: number): iObject | undefined {
        return undefined;
    }

    public extract(keys?: string[], target?: any) {
        const spec = target ? target : {};
        keys &&
            keys.forEach((key) => {
                spec[key] = Reflect.getOwnPropertyDescriptor(this, key).value;
            });
        return spec;
    }

    public override(properties?: any) {
        properties && Tools.overrideComputed(this, properties);
        return this;
    }

    public extend(properties?: any) {
        properties && Tools.extendComputed(this, properties);
        return this;
    }

    getMethodList() {
        //consider moveing this code to baseobject
        function getAllMethods(object: any) {
            return Object.getOwnPropertyNames(object).filter(function (property) {
                return typeof object[property] === 'function';
            });
        }

        const names = getAllMethods(this);

        const self = this;
        const obj1 = {};
        names.forEach((item) => {
            Reflect.set(obj1, item, Reflect.getOwnPropertyDescriptor(self, item).value);
            // obj1[item] = self[item](); //evaluate all methods
        });

        return names;
    }

    get debug() {
        return Tools.stringify(this);
        //return JSON.stringify(this,undefined,3);
    }

    get asJsonRaw() {
        const data = Tools.stringify(this);
        return JSON.parse(data);
    }

    get asJson() {
        return this.toJson();
    }
    protected toJson(): any {
        return {
            myName: this.myName,
            myGuid: this.myGuid,
            myType: this.myType
        };
    }

    protected jsonMerge(source: any) {
        const result = Tools.asJson(this);
        if (!Tools.isEmpty(source)) {
            Tools.forEachKeyValue(source, (key: string, value: any) => {
                if (!result[key] && !Tools.isEmpty(value)) {
                    const json = value && value.asJson;
                    result[key] = json ? json : value;
                }
            });
        }
        return result;
    }
}
