export class Logo extends IobtCanvasImage2D {
    imagePath = 'images';
    scale: number;
    name: string;
    code: string;
    filename: string;
    uniqueGuid: string;

    xLoc(): number {
        const parentPinX = (this.myParent() as foPage).pinX();
        return this.x - parentPinX;
    }

    yLoc(): number {
        return 5;
    }

    zLoc(): number {
        const parentPinY = (this.myParent() as foPage).pinY();
        return this.y - parentPinY;
    }

    asBody(): UDTO_Body {
        const parent = this.myParent() as typeof ParkTheatreCanvasPage;

        const bBox = new BoundingBox();
        bBox.override({
            units: 'm',
            width: 0.25,
            height: 1,
            depth: 0.25
        });

        const hiresPos = new UDTO_HighResPosition();
        hiresPos.override({
            xLoc: parent.scaleStageHorizontally(this.xLoc()),
            yLoc: this.yLoc(),
            zLoc: parent.scaleStageVertically(this.zLoc())
        });

        const body = new UDTO_Body();
        body.override({
            uniqueGuid: this.uniqueGuid,
            bodyName: this.code,
            position: hiresPos,
            boundingBox: bBox,
            platformName: parent.myName
        });

        return body;
    }

    finalize() {
        // this.imageURL = `${this.imagePath}/${this.filename}`;
        this.imageURL = `${this.filename}`;
    }

    constructor(properties?: any, parent?: foObject) {
        super(properties, parent);
        this.uniqueGuid = Tools.generateUUID();
        // this.override({
        //     width: properties.width * properties.scale,
        //     height: properties.height * properties.scale,
        //     scale: properties.scale
        // });
    }
}

export class AirForce extends Logo {
    get width() {
        return this.scale * 198;
    }
    get height() {
        return this.scale * 254;
    }
    scale = 0.3;
    code = 'airForce';
    name = 'Air Force';
    filename = airForceLogoImg;
}
