export class ParkTheatreCanvasPageClass extends foPage {
    private messageHandler: ParkTheatreMessage;
    private setEventSubscriptions() {
        Lifecycle.observable.subscribe((canvasLifecycleEvent) => {
            console.log(canvasLifecycleEvent);
            if (canvasLifecycleEvent.cmd === 'dropped') {
                const logo = canvasLifecycleEvent.object as Logo;
                this.messageHandler.sendBody(logo.asBody());
                K2PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
            } else if (canvasLifecycleEvent.cmd === 'moveComplete') {
                const logo = canvasLifecycleEvent.object as Logo;
                this.messageHandler.sendBody(logo.asBody());
                K2PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
            } else if (canvasLifecycleEvent.cmd === 'pageCreated') {
                this.messageHandler.sendPlatformMessage(this.asPlatform());
            }
        });
    }

    // stageImageURL = '/iobt-client-canvas/images/park-theatre/theatre-floor-plan.png';
    stageImageURL = stageImg;
    stageImageWidth = 1338;
    stageImageHeight = 1039;

    logoTypes: typeof Logo[] = [AirForce, Army, Navy, Marines, NASA, SpaceForce, FAA, VIP, Contractor];

    logos: Logo[] = [];

    showBoundry: false;

    theatreWidth = 48.4;
    theatreHeight = 100;
    theatreDepth = 37.58415;

    subHandle: foPubSub;

    get canvas2Params(): any {
        return {
            width: this.width,
            height: this.height,
            draw: (ctx: CanvasRenderingContext2D, count: number) => {
                this.render(ctx);
            }
        };
    }

    public scaleStageHorizontally(v: number): number {
        // assume stage is 48.4 m wide
        const retVal = this.theatreWidth * (v / this.width);
        return retVal;
    }

    public scaleStageVertically(v: number): number {
        // assume theatre is 37.5+ m deep
        return this.theatreDepth * (v / this.height);
    }

    public asPlatform(isIncludeBodies: boolean = false): UDTO_Platform {
        const bBox = new BoundingBox();
        bBox.override({
            units: 'm',
            width: this.theatreWidth,
            height: this.theatreHeight,
            depth: this.theatreDepth
        });

        const platform = new UDTO_Platform();
        platform.override({ uniqueGuid: platform.uniqueGuid, platformName: this.myName, boundingBox: bBox });

        if (isIncludeBodies) {
            platform.members = this.subcomponents.members.map((item) => {
                return (item as Logo).asBody();
            });
        }

        return platform;
    }

    public onItemHoverEnter = (loc: cPoint2D, shape: foGlyph2D, keys?: any): void => {
        shape.drawHover = shape.drawHighlight;
    };

    public onItemHoverExit = (loc: cPoint2D, shape: foGlyph2D, keys?: any): void => {
        shape.drawHover = undefined;
    };

    // Pin the page in the middle of the "A" in the word "STAGE"
    public pinX = (): number => 0.496 * this.width;
    public pinY = (): number => 0.217 * this.height;

    private createLogos() {
        this.logos = [];
        Object.values(this.logoTypes).forEach((item) => {
            const logo = new item(undefined, this);
            logo.scale = logo.scale * this.width * 0.001;
            // Do this to avoid "timing issue" with subclass instantiation
            logo.finalize();
            this.logos.push(logo);
        });
    }

    public init(getAvailableScreenWidth: () => number) {
        const stageImageScale = getAvailableScreenWidth() / this.stageImageWidth;
        this.width = this.stageImageWidth * stageImageScale;
        this.height = this.stageImageHeight * stageImageScale;

        const stage = new foImage2D({
            imageURL: this.stageImageURL,
            width: this.width,
            height: this.height
        });

        this.createLogos();

        this.postDraw = (ctx: CanvasRenderingContext2D) => {
            stage.draw(ctx);
            this.drawPin(ctx);
        };
    }

    public draw = (ctx: CanvasRenderingContext2D): void => {
        this.drawPage(ctx);
    };

    public dropLogo(logo: Logo) {
        logo.dropAt(this.width / 2, this.height / 2);
        this.subcomponents.addMember(logo);
    }

    constructor() {
        super();
        this.setName('ParkTheatre');
        this.messageHandler = new ParkTheatreMessage();
        this.setupMouseEvents();
        this.setEventSubscriptions();
    }
}

export const ParkTheatreCanvasPage = new ParkTheatreCanvasPageClass();
