import { IEnvVars, IServiceOptions } from './service-locator';
import { ModelBase } from './model-base';

export interface IHashServiceOptions {
    [key: string]: IServiceOptions;
}

class foEnvConfig extends ModelBase {
    production: boolean = false;

    baseURL: string;
    rootAPIPath: string;
    apiVersion: string;

    localMockPath: string = '/data';
    version: string = '0.0.0';

    envVars: IEnvVars;
    services: IHashServiceOptions = {};

    featureflags: any = {};

    constructor(properties?: any) {
        super();
        this.override(properties);
    }
}

export const EnvConfig = new foEnvConfig();
