import { ModelBase } from 'shared/model-base';
import configDevelopment from 'assets/config-local.json';
import configProduction from 'assets/config.json';
import { ServiceLocator } from 'shared/service-locator';
import { AppHttpService } from 'shared/app-http.service';
import { Environment } from 'environments/environment';
import { Subject } from 'rxjs';
import { CentralModelService } from './central-model.service';

export class AppService {
    static pingServer$(): Subject<boolean> {
        const localSubject = new Subject<boolean>();

        const serviceOptions = new ServiceLocator({
            serviceKey: 'pingServer$',
            endpoint: `/ping`
        });

        const http = AppHttpService.get$<ModelBase>(ModelBase, serviceOptions).subscribe(
            ({ message, error }) => {
                error && console.log(error);

                let health = !!message && message.toLowerCase().indexOf('up') >= 0;

                http.unsubscribe();
                localSubject.next(health);
                localSubject.complete();
            },
            (error) => {
                localSubject.error(error);
            }
        );

        return localSubject;
    }

    static loadConfiguration$(): Subject<boolean> {
        const localSubject = new Subject<boolean>();
        const env = process.env.NODE_ENV;

        // Decide which config file to use; assume development
        // let config: any = configProduction;

        let config: any = configDevelopment;
        if (env === 'production') {
            config = configProduction;
        }
        Environment.override(config);
        CentralModelService.init();

        window.setTimeout(() => {
            localSubject.next(true);
        }, 10);

        return localSubject;
    }
}
