import { createElement, ReactElement } from 'react';
import { Logo, ParkTheatreCanvasPage } from './park-theatre-canvas.model';
import { K2TextButtonItemComponents } from 'k2-mui-core/K2TextButtonItem';
import { ApplicationItemComponents, CommandListDirector, CommandListDirectorSpec, ICommandListOption } from 'shared/command';

export class ParkTheatreCommand {
    maxAllowedLogoCount = 3;
    private currentLogoCount = 0;

    private directDropLogo(logo: Logo): CommandListDirector {
        const spec: CommandListDirectorSpec = {
            label: ` + ${logo.name}`
        };

        return CommandListDirector.getInstance(spec)
            .renderRule((componentsContainer: ApplicationItemComponents) => {
                return this.currentLogoCount < this.maxAllowedLogoCount ? componentsContainer.default : componentsContainer.disabled;
            })
            .addSync({
                action: () => {
                    this.currentLogoCount++;
                    this.page.dropLogo(logo);
                }
            });
    }

    private createElement(render: any, props: ICommandListOption): ReactElement {
        return createElement(render, { key: props.id, ...props });
    }

    private generateOptions(commandListDirectors: CommandListDirector[], itemComponents: K2TextButtonItemComponents) {
        const options: ReactElement[] = commandListDirectors
            .filter((director) => {
                return director.existsWhenFunc();
            })
            .map((director, index) => {
                return this.createElement(director.getRender(itemComponents), {
                    id: `${director.label}`,
                    director
                });
            });
        return options;
    }

    private generateTextButtonActions(commandListDirectors: CommandListDirector[]): ReactElement[] {
        const itemComponents = new K2TextButtonItemComponents();
        return this.generateOptions(commandListDirectors, itemComponents);
    }

    public getTextButtons(force: boolean = false): ReactElement[] {
        let directors: CommandListDirector[];

        directors = this.page.logos.map((logo) => {
            return this.directDropLogo(logo);
        });

        return this.generateTextButtonActions(directors);
    }

    constructor(private page: typeof ParkTheatreCanvasPage) {}
}
