// import { Toast } from './shared/emitter.service';
import { ServiceLocator } from 'shared/service-locator';
import { TopicService } from 'services/topic.service';

import { Tools } from 'shared/foTools';
import {
    UDTO_Base,
    UDTO_ChatMessage,
    UDTO_Command,
    UDTO_Generic,
    UDTO_Objective,
    UDTO_Observation,
    UDTO_Position,
    UDTO_Status,
    Objective,
    UDTO_Platform,
    UDTO_Body
} from 'models/UDTO-message';
import { PubSub, Topic } from 'shared/K2PubSub';
import { LatLngTuple } from 'leaflet';
import { AppHttpService } from 'shared/app-http.service';
import { Feature, Point } from '@turf/turf';
import { Toast } from 'k2-core/Toast';

export class CentralModelServiceClass {
    http = AppHttpService;
    sharedModel: any = {};
    personModel: any = {};
    refreshedModel: any = {};
    isDisplayChatToast: boolean = true;
    private tService: TopicService;

    get summary(): any {
        return this.tService.summary;
    }

    addPropertyToTarget(target: any, name: string, value: any) {
        if (!target[name]) {
            target[name] = [];
        }
        target[name].unshift(value);
        return target[name];
    }

    addProperty(name: string, value: any) {
        return this.addPropertyToTarget(this.sharedModel, name, value);
    }

    removeSource(target: any, sourceGuid: string, personId: string) {
        const key = 'sources';
        const source = sourceGuid;

        if (this.personModel[personId]) {
            delete this.personModel[personId];
        }

        if (target[key] && target[key][source]) {
            delete target[key][source];
        }
    }

    establishSource(target: any, topic: string, data: UDTO_Base) {
        const key = 'sources';
        const source = data.sourceGuid;
        const personId = data.personId;

        if (!this.personModel[personId]) {
            this.personModel[personId] = source;
        }

        if (!target[key]) {
            target[key] = {};
        }
        if (!target[key][source]) {
            target[key][source] = {};
        }
        const root = target[key][source];
        if (!root[topic]) {
            root[topic] = {};
        }
        root[topic] = data;
        return root;
    }

    removeObservation(target: any, uniqueGuid: string) {
        const key = 'sources';
        const objectiveKey = 'observations';

        Reflect.ownKeys(target[key]).forEach((currentGuid) => {
            const observations = target[key][currentGuid][objectiveKey];
            if (observations) {
                target[key][currentGuid][objectiveKey] = observations.filter((objective: UDTO_Observation) => {
                    return objective.uniqueGuid !== uniqueGuid;
                });
            }
        });
    }

    establishObservation(target: any, topic: string, data: UDTO_Observation) {
        const key = 'sources';
        const source = data.sourceGuid;

        if (!target[key]) {
            target[key] = {};
        }
        if (!target[key][source]) {
            target[key][source] = {};
        }
        const root = target[key][source];
        if (!root[topic]) {
            root[topic] = [];
        }
        const found = root[topic].find((item: UDTO_Observation) => {
            return item.uniqueGuid === data.uniqueGuid;
        });

        if (found) {
            found.override(data);
        } else {
            root[topic].unshift(data);
        }

        return root;
    }

    establishPlatform(target: any, name: string, data: UDTO_Platform) {
        const key = 'platforms';

        if (!target[key]) {
            target[key] = {};
        }
        const root = target[key];
        root[name] = data;

        return root;
    }

    retrievePlatform(target: any, name: string): UDTO_Platform {
        const key = 'platforms';

        if (!target[key]) {
            target[key] = {};
        }
        const root = target[key];
        return root[name] || {};
    }

    addObservation(topic: string, data: UDTO_Observation) {
        return this.establishObservation(this.sharedModel, topic, data);
    }

    addPlatform(data: UDTO_Platform) {
        return this.establishPlatform(this.sharedModel, data.platformName, data);
    }

    removeObjective(target: any, uniqueGuid: string) {
        const key = 'sources';
        const objectiveKey = 'objectives';

        Reflect.ownKeys(target[key]).forEach((currentGuid) => {
            const objectives = target[key][currentGuid][objectiveKey];
            if (objectives) {
                target[key][currentGuid][objectiveKey] = objectives.filter((objective: UDTO_Objective) => {
                    return objective.uniqueGuid !== uniqueGuid;
                });
            }
        });
    }

    establishObjective(target: any, topic: string, data: UDTO_Objective) {
        const key = 'sources';
        const source = data.sourceGuid;

        if (!target[key]) {
            target[key] = {};
        }
        if (!target[key][source]) {
            target[key][source] = {};
        }
        const root = target[key][source];
        if (!root[topic]) {
            root[topic] = [];
        }
        const found = root[topic].find((item: UDTO_Observation) => {
            return item.uniqueGuid === data.uniqueGuid;
        });

        if (found) {
            found.override(data);
        } else {
            root[topic].unshift(data);
        }

        return root;
    }

    addToSource(topic: string, data: UDTO_Base) {
        return this.establishSource(this.sharedModel, topic, data);
    }

    addObjective(topic: string, data: UDTO_Objective) {
        const obj = new UDTO_Objective(data);
        return this.establishObjective(this.sharedModel, topic, obj);
    }

    processAllSourceData(data: any) {
        const sources = data['sources'];
        Tools.forEachKeyValue(sources, (key: string, value: any) => {
            // Toast.warning(key, 'adding source');
            console.log('adding source key=', key);

            const position = value['position'];
            position && this.addToSource('position', new UDTO_Position(position));

            const objectives = value['objectives'];
            if (objectives) {
                this.sharedModel['sources'][key]['objectives'] = [];
                Tools.forEachKeyValue(objectives, (objKey: any, objValue: any) => {
                    this.addObjective('objectives', new UDTO_Objective(objValue));
                });
            }

            const observations = value['observations'];
            if (observations) {
                this.sharedModel['sources'][key]['observations'] = [];
                Tools.forEachKeyValue(observations, (objKey: any, objValue: any) => {
                    this.addObservation('observations', new UDTO_Observation(objValue));
                });
            }
        });

        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    constructor() {
        this.tService = new TopicService();
    }

    init(): void {
        this.tService.doStart(
            () => {
                // Toast.info('after tService.doStart');
                console.log('after tService.doStart');
                this.tService.Pong$().subscribe((result) => {
                    this.receivePing(result);
                    //Toast.success('CentralModel Pong', result);
                });

                this.tService.Generic$().subscribe((result) => {
                    this.receiveGeneric(result);
                    //Toast.success('CentralModel Generic', Tools.stringify(result));
                });

                this.tService.ChatMessage$().subscribe((result) => {
                    this.receiveChatMessage(result);
                    // this.isDisplayChatToast && Toast.success('CentralModel ChatMessage', Tools.stringify(result));
                    this.isDisplayChatToast && console.log('CentralModel ChatMessage', Tools.stringify(result));
                });

                this.tService.Position$().subscribe((result) => {
                    this.receivePosition(result);
                    // Toast.success('CentralModel Position', Tools.stringify(result));
                });

                this.tService.Objective$().subscribe((result) => {
                    this.receiveObjective(result);
                    // Toast.success('CentralModel Objective', Tools.stringify(result));
                });

                this.tService.Observation$().subscribe((result) => {
                    this.receiveObservation(result);
                    // Toast.success('CentralModel Objective', Tools.stringify(result));
                });

                this.tService.Platform$().subscribe((result) => {
                    this.receivePlatform(result);
                    // Toast.success('CentralModel Platform', Tools.stringify(result));
                });

                this.tService.Body$().subscribe((result) => {
                    this.receiveBody(result);
                    // Toast.success('CentralModel Platform', Tools.stringify(result));
                });

                this.tService.Status$().subscribe((result) => {
                    this.receiveStatus(result);
                    // Toast.success('CentralModel Status', Tools.stringify(result));
                });

                this.tService.Command$().subscribe((result) => {
                    this.receiveCommand(result);
                    this.processCommand(result);
                    // Toast.success('CentralModel Command', Tools.stringify(result));
                });

                PubSub.broadcastToLateObservers(Topic.SIGNAL_R_CONNECTION_ESTABLISHED);
                this.register();
            },

            () => {
                // Toast.error('SignalR server did not start');
                console.error('SignalR server did not start');
                PubSub.broadcastToLateObservers(Topic.SIGNAL_R_CONNECTION_UNAVAILABLE);
            }
        );
    }

    public restartEvents() {
        this.init();
    }

    public stopEvents() {
        this.tService.doStop(() => {
            // Toast.error('Service stopped');
            console.error('Service stopped');
        });
    }

    public register() {
        this.sendStatus();
        this.sendPosition();
    }

    public unRegister() {
        this.unregisterSource(this.tService.sourceGUID, this.tService.personId);
    }

    public unregisterSource(guid: string, personId: string) {
        const cmd = new UDTO_Command({
            targetGuid: guid,
            command: 'UNREGISTER',
            args: [personId, guid]
        });
        this.processCommand(cmd);
        this.tService.sendCommand(cmd);
    }

    public deleteObservation(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'DELETE_OBSERVATION'
        });
        this.processCommand(cmd);
        this.tService.sendCommand(cmd);
    }

    public deleteObjective(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'DELETE_OBJECTIVE'
        });
        this.processCommand(cmd);
        this.tService.sendCommand(cmd);
    }

    public deleteObjectivePossibleTargets() {
        Tools.forEachKeyValue(this.sharedModel.sources, (sourceGuid: string, context: any) => {
            const keepObjectives = context.objectives?.filter((observation: UDTO_Objective) => {
                return observation.type !== Objective.POSSIBLE_TARGET;
            });
            context.objectives = keepObjectives;
        });
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    public zoomInMap(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'ZOOM_IN'
        });
        this.tService.sendCommand(cmd);
    }

    public zoomOutMap(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'ZOOM_OUT'
        });
        this.tService.sendCommand(cmd);
    }

    public hideMap(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'HIDE_MAP'
        });
        this.tService.sendCommand(cmd);
    }

    public showMap(uniqueGuid: string) {
        const cmd = new UDTO_Command({
            targetGuid: uniqueGuid,
            command: 'SHOW_MAP'
        });
        this.tService.sendCommand(cmd);
    }

    processCommand(data: UDTO_Command) {
        if (Tools.matches(data.command, 'UNREGISTER')) {
            const guid = data.targetGuid;
            const personId = data.args[0];
            this.removeSource(this.sharedModel, guid, personId);
            PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
        }

        if (Tools.matches(data.command, 'FLUSH')) {
            this.sharedModel = {};
            this.personModel = {};
            this.refreshedModel = {};
            PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
        }

        if (Tools.matches(data.command, 'DELETE_OBJECTIVE')) {
            const uniqueGuid = data.targetGuid;
            this.removeObjective(this.sharedModel, uniqueGuid);
            PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
        }

        if (Tools.matches(data.command, 'DELETE_OBSERVATION')) {
            const uniqueGuid = data.targetGuid;
            this.removeObservation(this.sharedModel, uniqueGuid);
            PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
        }

        if (Tools.matches(data.command, 'ZOOM_IN')) {
            PubSub.broadcast(Topic.RECEIVED_ZOOM_IN_COMMAND);
        }

        if (Tools.matches(data.command, 'ZOOM_OUT')) {
            PubSub.broadcast(Topic.RECEIVED_ZOOM_OUT_COMMAND);
        }

        if (Tools.matches(data.command, 'HIDE_MAP')) {
            PubSub.broadcast(Topic.RECEIVED_HIDE_MAP_COMMAND);
        }

        if (Tools.matches(data.command, 'SHOW_MAP')) {
            PubSub.broadcast(Topic.RECEIVED_SHOW_MAP_COMMAND);
        }
    }

    public refreshFromServer(done?: () => void) {
        const serviceOptions = new ServiceLocator({
            serviceKey: 'refreshFromServer$',
            endpoint: `/ClientHub/CentralModel`
        });

        const httpSubject = this.http.getAny$(serviceOptions);

        httpSubject.subscribe(
            (result) => {
                if (result.hasError) {
                    // Toast.error(result.message);
                    console.error(result.message);
                } else {
                    this.refreshedModel = result.payload[0];
                    this.processAllSourceData(this.refreshedModel);
                    // Toast.success('refreshing');
                    console.log('refreshing');
                }
                done && done();
            },
            (error) => {
                // Toast.error(error);
                console.error(error);
            }
        );
    }

    public flushCentralModel(done?: () => void) {
        const serviceOptions = new ServiceLocator({
            serviceKey: 'refreshFromServer$',
            endpoint: `/ClientHub/flushCentralModel`
        });

        const httpSubject = this.http.getAny$(serviceOptions);

        httpSubject.subscribe(
            (result) => {
                if (result.hasError) {
                    Toast.error(result.message);
                } else {
                    this.sharedModel = {};
                    this.personModel = {};
                    this.refreshedModel = {};
                    PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
                }
                done && done();
            },
            (error) => {
                Toast.error(error);
                console.error(error);
            }
        );
    }

    sendPing() {
        this.tService.doPing();
    }

    receivePing(data: any) {
        console.log('received ping data=', data);
        this.addProperty('ping', data);
    }

    sendPingPong() {
        this.tService.doPingPong();
    }

    sendGeneric() {
        this.tService.sendGeneric(new UDTO_Generic({}));
    }

    receiveGeneric(data: UDTO_Base) {
        this.addToSource('generic', data);
    }

    sendChatMessageMesh(data: UDTO_ChatMessage = new UDTO_ChatMessage({})) {
        this.tService.sendChatMessageMesh(data);
    }

    receiveChatMessage(data: UDTO_ChatMessage) {
        this.addToSource('last_message', data);
        PubSub.broadcastPayload(Topic.RECEIVED_CHAT_MESSAGE, data);
    }

    sendPosition() {
        this.tService.sendPosition(
            new UDTO_Position({
                lat: this.tService.lastLat,
                lng: this.tService.lastLng
            })
        );
    }

    receivePosition(data: UDTO_Base) {
        this.addToSource('position', data);
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    sendObjective(data: UDTO_Objective) {
        this.tService.sendObjective(data);
    }

    receiveObjective(data: UDTO_Objective) {
        this.addObjective('objectives', data);
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    sendObservation(data: UDTO_Observation): UDTO_Observation {
        // return this.tService.sendObservation(new UDTO_Observation(data));
        return this.tService.sendObservation(data);
    }

    receiveObservation(data: UDTO_Observation) {
        this.addObservation('observations', data);
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    sendPlatform(data: UDTO_Platform): UDTO_Platform {
        return this.tService.sendPlatform(data);
    }

    receivePlatform(data: UDTO_Platform) {
        this.addPlatform(data);
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    sendBody(data: UDTO_Body): UDTO_Body {
        return this.tService.sendBody(data);
    }

    receiveBody(data: UDTO_Body) {
        const platform = this.retrievePlatform(this.sharedModel, data.platformName);
        platform.establishBody(data);
        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    computeTargetsFromObservations() {
        const list: Array<UDTO_Observation> = [];

        Reflect.ownKeys(this.sharedModel.sources || {})
            .sort()
            .forEach((sourceGuid) => {
                const sourceContext = this.sharedModel.sources[sourceGuid];

                sourceContext.observations?.forEach((observation: UDTO_Observation) => {
                    list.push(observation);
                });
            });

        for (var i = 0; i < list.length; i++) {
            const obs1: UDTO_Observation = list[i];
            for (var j = 0; j < list.length; j++) {
                if (i === j) {
                    continue;
                }

                const obs2: UDTO_Observation = list[j];
                this.computeObjective(obs1, obs2, 'target');
            }
        }

        PubSub.broadcast(Topic.RECEIVED_COP_UPDATE);
    }

    createPossableTarget(point: Feature<Point>, objName: string) {
        const target = new UDTO_Objective({
            lat: UDTO_Base.asLat(point),
            lng: UDTO_Base.asLng(point),
            uniqueGuid: Tools.generateUUID(),
            name: 'assets/images/ICON_Enemy.png',
            type: Objective.POSSIBLE_TARGET,
            note: `${objName} Computed from Observations`
        });

        this.tService.sendObjective(target);
        return target;
    }

    computeObjective(obj1: UDTO_Observation, obj2: UDTO_Observation, objName: string) {
        const pts = obj1.intersection(obj2);
        if (pts.length === 0) {
            return false;
        }

        const p1 = pts[0];
        if (p1) {
            this.createPossableTarget(p1, objName);
        }

        const p2 = pts[1];
        if (p2) {
            this.createPossableTarget(p1, objName);
        }
        return true;
    }

    resetPosition(done?: (position: LatLngTuple) => void) {
        this.tService.resetPosition(() => {
            this.sendPosition();
            done && done([this.tService.lastLat, this.tService.lastLng]);
        });
    }

    moveAndSendPosition(deltaLat: number, deltaLng: number) {
        this.tService.movePosition(deltaLat, deltaLng);
        this.sendPosition();
    }

    sendStatus() {
        this.tService.sendStatus(
            new UDTO_Status({
                user: this.tService.personId,
                device: 'Squire',
                ammo: 100,
                heartrate: 62,
                ip_address: '0.0.0.0',
                battery: 'full',
                notes: 'Ready'
            })
        );
    }

    receiveStatus(data: UDTO_Base) {
        this.addToSource('status', data);
    }

    sendCommand(data?: any) {
        this.tService.sendCommand(new UDTO_Command(data));
    }

    receiveCommand(data: UDTO_Base) {
        this.addToSource('command', data);
    }
}

export const CentralModelService = new CentralModelServiceClass();
