import { ModelBase } from 'shared/model-base';
import { Observable, of, Subject } from 'rxjs';
import { switchMap, catchError } from 'rxjs/operators';
import { AjaxResponse, ajax } from 'rxjs/ajax';
import { Constructable, FuncAny } from 'shared/types';
import { ServiceLocator } from './service-locator';
// import { Toast } from './emitter.service';

export interface IResponse<T> {
    dateTime: string;
    hasError: boolean;
    status: string;
    length: number;
    message: string;
    payload: Array<T>;
    payloadType: string;
    metadata: any;
    error: any;
}

//TODO: see what rxjs error response returns
export interface HttpErrorResponse {
    message: string;
    error: string;
    status: number;
    statusText: string;
}

export class payloadHandler {
    static success<T>(type: Constructable<T>, successResponse: AjaxResponse<any>, mapFunc?: FuncAny): Observable<IResponse<T>> {
        const res: IResponse<T> = successResponse.response;
        res.payload = AppHttpService.mapToModel(type, res.payload, mapFunc);
        if (res.status === 'pending') {
            const warningMessage = `${res.status} ${res.message} ${successResponse.request.url}`;
            // Toast.warning(warningMessage);
            console.warn(warningMessage);
        }
        return of<IResponse<T>>(res);
    }

    static getAnySuccess(successResponse: AjaxResponse<any>): Observable<IResponse<any>> {
        const res: IResponse<any> = successResponse.response;
        if (res.status === 'pending') {
            const warningMessage = `${res.status} ${res.message} ${successResponse.request.url}`;
            // Toast.warning(warningMessage);
            console.warn(warningMessage);
        }
        return of<IResponse<any>>(res);
    }

    static error<T>(errorResponse: Readonly<HttpErrorResponse>): Observable<IResponse<T>> {
        return of<IResponse<T>>({ ...AppHttpService.mapErrorResponse(errorResponse) });
    }
}

export class AppHttpServiceClass {
    static makeHeaders(additionalHeaders: any, contentType?: any) {
        const token: any = null; //CurrentUser?.Token.accessToken;
        if (token) {
            const bearerToken = { Authorization: `Bearer ${token}` };
            return { ...bearerToken, ...additionalHeaders, ...contentType };
        } else {
            return { ...additionalHeaders, ...contentType };
        }
    }

    mapToModel<T>(type: Constructable<T>, payload: Array<any>, func?: FuncAny): Array<T> {
        const list = payload.map<T>((item) => {
            const data = func ? func(item) : item;
            return new type(data);
        });
        return list;
    }

    // mapErrorResponse prevents consumers of http services from directly handling HttpErrorResponses
    mapErrorResponse<T>(error: HttpErrorResponse): IResponse<T> {
        const formattedError: IResponse<T> = {
            dateTime: new Date().toISOString(),
            hasError: true,
            status: 'error',
            length: -1,
            payload: [],
            payloadType: '',
            metadata: {},
            message: error.message,
            error
        };
        return formattedError;
    }

    public post$<T extends ModelBase>(type: Constructable<T>, serviceOptions: ServiceLocator, data: T, additionalHeaders?: any, func?: FuncAny): Subject<IResponse<T>> {
        const subject = new Subject<IResponse<T>>();
        const url = serviceOptions.getAPIUrl();

        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders, { 'Content-Type': 'application/json' });

        console.log('post$ url=', url);
        ajax.post(url, data, headers)
            .pipe(
                switchMap((ajaxRes: AjaxResponse<any>) => {
                    return payloadHandler.success(type, ajaxRes, func);
                }),
                catchError((error: HttpErrorResponse) => {
                    console.log(JSON.stringify(data, undefined, 3));
                    return payloadHandler.error<T>(error);
                })
            )
            .subscribe(subject);

        return subject;
    }

    public postAny$<A extends ModelBase, B>(type: Constructable<A>, serviceOptions: ServiceLocator, data: B, additionalHeaders?: any, func?: FuncAny): Subject<IResponse<A>> {
        const subject = new Subject<IResponse<A>>();
        let first = true;
        let url = serviceOptions.getAPIUrl();
        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders, { 'Content-Type': 'application/json' });

        for (const name in data) {
            const value = data[name];
            if (value instanceof Array) {
                for (let aValue of value) {
                    url += (first ? '?' : '&') + encodeURIComponent(name.trim()) + '=' + encodeURIComponent(typeof aValue == 'string' ? aValue.trim() : JSON.stringify(aValue));
                    first = false;
                }
            } else {
                url += (first ? '?' : '&') + encodeURIComponent(name.trim()) + '=' + encodeURIComponent(typeof value == 'string' ? value.trim() : JSON.stringify(value));
                first = false;
            }
        }

        console.log('postAny$ url=', url);
        ajax.post(url, null, headers)
            .pipe(
                switchMap((ajaxRes: AjaxResponse<any>) => {
                    return payloadHandler.success(type, ajaxRes, func);
                }),
                catchError((error: HttpErrorResponse) => {
                    console.log(JSON.stringify(data, undefined, 3));
                    return payloadHandler.error<A>(error);
                })
            )
            .subscribe(subject);

        return subject;
    }

    public postAnyData$<A extends ModelBase, B>(type: Constructable<A>, serviceOptions: ServiceLocator, data: B, additionalHeaders?: any, func?: FuncAny): Subject<IResponse<A>> {
        const subject = new Subject<IResponse<A>>();
        let url = serviceOptions.getUrl();
        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders, { 'Content-Type': 'application/json' });

        console.log('postAny$ url=', url);
        ajax.post(url, data, headers)
            .pipe(
                switchMap((ajaxRes: AjaxResponse<any>) => {
                    return payloadHandler.success(type, ajaxRes, func);
                }),
                catchError((error: HttpErrorResponse) => {
                    console.log(JSON.stringify(data, undefined, 3));
                    return payloadHandler.error<A>(error);
                })
            )
            .subscribe(subject);

        return subject;
    }

    public put$<T extends ModelBase>(type: Constructable<T>, serviceOptions: ServiceLocator, data: T, additionalHeaders?: any, func?: FuncAny): Subject<IResponse<T>> {
        const subject = new Subject<IResponse<T>>();
        const url = serviceOptions.getUrl();
        console.log('put$ url=', url);

        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders);

        ajax.put(url, JSON.stringify(data), headers)
            .pipe(
                switchMap((ajaxRes: AjaxResponse<any>) => {
                    return payloadHandler.success(type, ajaxRes, func);
                }),
                catchError((error: HttpErrorResponse) => {
                    return payloadHandler.error<T>(error);
                })
            )
            .subscribe(subject);

        return subject;
    }

    public get$<T extends ModelBase>(type: Constructable<T>, serviceOptions: ServiceLocator, additionalHeaders?: any, func?: FuncAny): Subject<IResponse<T>> {
        const subject = new Subject<IResponse<T>>();
        const url = serviceOptions.getAPIUrl();

        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders);

        console.log('getAPIUrl', url);

        ajax.get(url, headers)
            .pipe(
                switchMap((ajaxRes: AjaxResponse<any>) => {
                    const responce = ajaxRes.response;
                    // This if statement will re-run the query, but for mock data, if a path is supplied for mock data and the response has the pending status
                    if (responce.status === 'pending') {
                        if (responce.length <= 0 && serviceOptions.localDataPath) {
                            let options = new ServiceLocator({
                                ...serviceOptions,
                                forceLocalMock: true
                            });
                            return this.get$<T>(type, options, func);
                        } else {
                            // Toast.warning(`${responce.length} records found: PENDING status may not be necessary`);
                            console.warn(`${responce.length} records found: PENDING status may not be necessary`);
                            return payloadHandler.success(type, ajaxRes, func);
                        }
                    }
                    return payloadHandler.success(type, ajaxRes, func);
                }),
                catchError((error: HttpErrorResponse) => {
                    //Toast.error(url, 'HTTP Issue');
                    console.error(url, 'HTTP Issue');

                    return payloadHandler.error<T>(error);
                })
            )
            .subscribe(subject);

        return subject;
    }

    public getAny$(serviceOptions: ServiceLocator, additionalHeaders?: any): Observable<IResponse<any>> {
        const url = serviceOptions.getAPIUrl();

        console.log('getAny$ url', url);

        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders);

        return ajax.get(url, headers).pipe(
            switchMap((ajaxRes: AjaxResponse<any>) => {
                return payloadHandler.getAnySuccess(ajaxRes);
            }),
            catchError((error: HttpErrorResponse) => {
                // Toast.error(url, 'HTTP Issue');
                console.error(url, 'HTTP Issue');
                return payloadHandler.error<any>(error);
            })
        );
    }

    public rawFetch$(url: string, additionalHeaders?: any): Observable<any> {
        const headers = AppHttpServiceClass.makeHeaders(additionalHeaders);

        return ajax.get(url, headers).pipe(
            switchMap((ajaxRes: AjaxResponse<any>) => {
                return of(ajaxRes.response);
            }),
            catchError((error: HttpErrorResponse) => {
                // Toast.error(url, 'HTTP Issue');
                console.error(url, 'HTTP Issue');
                return of(error);
            })
        );
    }
}

export const AppHttpService = new AppHttpServiceClass();
